<template>
  <div>
    <b-row>
      <!-- progressbar -->
      <div class="col-md-12">
        <div class="progressbar-container text-center">
          <ul class="progressbar">
            <li class="progressbar-active">檢視購物車</li>
            <li>付款方式及寄送資訊</li>
            <li>確認訂單資料</li>
            <li>訂單成立</li>
          </ul>
        </div>
      </div>
      <!-- progressbar -->
    </b-row>

    <b-row class="mt-3">
      <b-col lg="12" md="12" class>
        <b-tabs>
          <b-tab title="購物明細">
            <b-form>
              <!-- https://bootstrap-vue.org/docs/components/table -->
              <!-- <b-table hover stacked="md" selectable :select-mode="selectMode" @row-selected="onRowSelected" ref="selectableTable" :fields="fields" :items="items"> -->
              <b-table
                striped
                hover
                stacked="lg"
                ref="selectableTable"
                :fields="fields"
                :items="items"
                :head-variant="headVariant"
              >
                <!-- head scoped slot -->
                <template v-slot:head(checkbox)>
                  <input
                    type="checkbox"
                    v-model="allSelected"
                    @change="toggleAll(allSelected)"
                    class="checkAll"
                    id="checkAll"
                    name="checkAll"
                  />
                </template>
                <!-- Optional default data cell scoped slot -->
                <template v-slot:cell()="data">
                  <span v-text="data.value"></span>
                </template>
                <!-- A custom formatted column -->
                <template v-slot:cell(checkbox)="data">
                  <input
                    type="checkbox"
                    :value="data.item.isbn"
                    :id="'checkbox' + data.item.isbn"
                    :checked="data.item.checked"
                    :key="'checkbox' + data.item.isbn"
                    @change="setChecked($event)"
                  />
                </template>
                <template v-slot:cell(pic)="data">
                  <router-link :to="'/product/' + data.item.isbn">
                    <img
                      :src="data.item.src"
                      alt
                      class
                      style="display: block; margin: 0 auto; height: 100px"
                    />
                  </router-link>
                </template>
                <!-- A custom formatted column -->
                <template v-slot:cell(name)="data">
                  <router-link
                    class="text-decoration-none text-primary"
                    :to="'/product/' + data.item.isbn"
                  >
                    <span v-text="data.value"></span>
                  </router-link>
                </template>
                <!-- A custom formatted column -->
                <template v-slot:cell(salePrice)="data">
                  <span v-text="salePrice(data)"></span>
                </template>
                <!-- A custom formatted column -->
                <template v-slot:cell(quantity)="data">
                  <input
                    type="number"
                    min="1"
                    max="100"
                    :value="data.item.quantity"
                    :class="validateClass(data.item.quantityState)"
                    @input="changeQuantity($event, data.item.isbn)"
                    style="width: 90px"
                    :key="'quantity' + data.item.isbn"
                    oncut="return false;"
                    onpaste="return false;"
                  />
                  <div
                    class="mt-1 text-danger"
                    v-if="isSmallAmount(data.item.isbn)"
                  >
                    只剩&nbsp;{{ getInventory(data.item.isbn) }}&nbsp;件
                  </div>
                  <!-- <div>
                    <b-button
                      variant="dark"
                      class="mt-1"
                      @click="changeInventory(data.item.isbn)"
                      >修改庫存</b-button
                    >
                  </div> -->
                </template>
                <!-- A custom formatted column -->
                <template v-slot:cell(subtotal)="data">
                  <span v-text="calSubTotal(data)"></span>
                </template>
                <template v-slot:cell(changeItem)="data">
                  <b-button
                    variant="outline-dark"
                    @click="deleteItem({ isbn: data.item.isbn })"
                    >刪除</b-button
                  >
                </template>
              </b-table>
              <div class="d-flex bg-light">
                <div class="p-3 mr-auto">
                  <b-button variant="outline-dark" @click="deleteSelected"
                    >刪除勾選項目</b-button
                  >
                </div>
                <div class="d-flex align-items-center">
                  <p class="mt-3 mr-3">
                    共
                    <span class="text-danger">{{ items.length }}</span>
                    項商品，數量
                    <span class="text-danger" v-text="totalQuantity()"></span>
                    個，總金額 NT$
                    <span class="text-danger" v-text="totalAmount()"></span> 元
                  </p>
                </div>
              </div>
              <div class="text-right mt-3">
                <b-button class="mr-3" variant="outline-primary" @click="goHome"
                  >繼續購物</b-button
                >
                <b-button
                  class="mr-3"
                  :disabled="
                    items.length == 0 ||
                    isNaN(totalQuantity()) ||
                    totalQuantity() == 0
                  "
                  variant="outline-primary"
                  @click="goDeliver"
                  >結帳</b-button
                >
              </div>
            </b-form>
          </b-tab>
        </b-tabs>
      </b-col>
    </b-row>

    <!-- <b-modal ref="msg-modal" hide-footer title="Demo Site">
      <div class="d-block text-center">
        <h4>{{ msg }}</h4>
      </div>
    </b-modal> -->
  </div>
</template>
 
<script>
import styles from "@/assets/css/progress-bar.css";
import config from "@/common/config";
import { mapActions } from "vuex";
import store from "@/store";
import ApiService from "@/common/api.service";

export default {
  name: "Shoppingcart",
  data() {
    return {
      // https://bootstrap-vue.org/docs/components/table#row-select-support
      // selectMode: "multi",
      // onRowSelected(items) {
      //   this.selected = items
      // },
      allSelected: false,
      headVariant: "dark", // Use 'light' or 'dark' to make table header appear light or dark gray, respectively
      fields: config.shoppingcartFields,
      // msg: null,
    };
  },
  components: {},
  mounted() {},
  // https://vuejs.org/v2/guide/instance.html#Lifecycle-Diagram
  // https://cn.vuejs.org/v2/guide/instance.html#%E7%94%9F%E5%91%BD%E5%91%A8%E6%9C%9F%E5%9B%BE%E7%A4%BA
  beforeCreate() {
    //this.$store.dispatch("cart/refresh");
    console.log("Shoppingcart.vue beforeCreate");
  },
  beforeRouteEnter(to, from, next) {
    console.log("Shoppingcart.vue beforeRouteEnter");
    let cartProducts = store.getters["cart/cartProducts"];
    let isbnArray = store.getters["cart/cartProducts"].map((item) => {
      return item.isbn;
    });
    console.log("Shoppingcart.vue beforeRouteEnter isbnArray", isbnArray);
    if (isbnArray.length) {
      ApiService.post("products/inventory", {
        isbn: isbnArray,
      })
        .then((response) => {
          console.log(
            "Shoppingcart.vue beforeRouteEnter response : " +
              JSON.stringify(response)
          );
          // https://router.vuejs.org/zh/guide/advanced/data-fetching.html#%E5%9C%A8%E5%AF%BC%E8%88%AA%E5%AE%8C%E6%88%90%E5%89%8D%E8%8E%B7%E5%8F%96%E6%95%B0%E6%8D%AE
          next((vm) => {
            response.data.forEach((item) => {
              vm.updateInventory(item);
              let cartProd = cartProducts.find(
                (element) => element.isbn === item.isbn
              );
              if (item.inventory < cartProd.quantity) {
                vm.setQuantityInCart(item.isbn, item.inventory);
              }
            });
          });
        })
        // Axios error.response
        // https://github.com/axios/axios/issues/960
        .catch((err) => {
          console.log("err : " + err);
          console.log("err.response : " + JSON.stringify(err.response));
        });
    } else {
      next();
    }
  },
  computed: {
    items() {
      return this.$store.getters["cart/cartProducts"];
    },
    products() {
      return this.$store.getters["products/getItems"];
    },
  },
  methods: {
    validateClass(state) {
      return {
        "form-control": true,
        "is-invalid": state === null ? false : true,
      };
    },
    updateInventory(product) {
      console.log("Shoppingcart.vue updateInventory", JSON.stringify(product));
      this.$store.dispatch("products/changeInventory", product);
    },
    goHome() {
      this.$router.push({ name: "index" });
    },
    goDeliver() {
      let emptyQuantityArray = this.items.filter(
        (item) => item.quantity.length == 0
      );
      console.log(JSON.stringify(emptyQuantityArray));
      if (!emptyQuantityArray.length) {
        this.$router.push({ name: "deliver" });
      } else {
        for (let item of emptyQuantityArray) {
          this.$store
            .dispatch("cart/changeQuantityState", {
              isbn: item.isbn,
              quantityState: false,
            })
            .then((response) => {
              console.log("changeQuantityState response", response);
            })
            .catch((err) => {
              console.log("changeQuantityState err", err);
            });
          this.$toastr.e("請填入正確的數量");
        }
      }
    },
    getInventory(isbn) {
      let product = this.products.find((item) => item.isbn === isbn);
      return !!product ? product.inventory : 0;
    },
    // changeInventory(isbn) {
    //   // this.$store.dispatch("products/changeInventory", { isbn, inventory: 3 });
    //   this.$store.dispatch("products/changeInventory", { isbn, inventory: 0 });
    // },
    isSmallAmount(isbn) {
      return this.getInventory(isbn) <= 10;
    },
    salePrice(data) {
      // return Math.round(data.item.listPrice * data.item.discount / 100);
      data.item.salePrice = this.calSalePrice(
        data.item.listPrice,
        data.item.discount
      );
      return data.item.salePrice;
    },
    calSalePrice(price, discount) {
      return Math.round((price * discount) / 100);
    },
    calSubTotal(data) {
      // return this.salePrice(data) * data.item.quantity;
      return data.item.salePrice * data.item.quantity;
    },
    setChecked(event) {
      //console.log(this.checkedBooks);
      console.log(event.target.id, event.target.checked, event.target.value);
      this.$store.dispatch("cart/setChecked", {
        isbn: event.target.value,
        checked: event.target.checked,
      });
    },
    setQuantityInCart(isbn, quantity) {
      this.$store
        .dispatch("cart/changeQuantity", {
          isbn,
          quantity,
        })
        .then((response) => {
          console.log("setQuantityInCart response", response);
          //this.msg = response; this.$refs['msg-modal'].show();
        })
        .catch((err) => {
          // this.msg = err;
          // this.$refs["msg-modal"].show();
          console.log("setQuantityInCart err", err);
        });
    },
    changeQuantity(event, isbn) {
      console.log(
        event.target.value,
        Number.isInteger(event.target.value),
        isbn
      );
      let quantity = 1;
      if (
        Number.isInteger(parseInt(event.target.value, 10)) &&
        parseInt(event.target.value, 10) > 0
      ) {
        if (parseInt(event.target.value, 10) < 100) {
          quantity = parseInt(event.target.value, 10);
        } else {
          quantity = 100;
        }
      } else if (event.target.value.length == 0) {
        quantity = event.target.value;
      }
      // clear quantityState
      this.$store
        .dispatch("cart/changeQuantityState", {
          isbn,
          quantityState: null,
        })
        .then((response) => {
          console.log("changeQuantityState response", response);
        })
        .catch((err) => {
          console.log("changeQuantityState err", err);
        });
      console.log("changeQuantity", event.target.value, quantity);
      this.$store
        .dispatch("cart/changeQuantity", {
          isbn: isbn,
          // quantity: parseInt(event.target.value, 10),
          quantity,
        })
        .then((response) => {
          console.log("response", response);
          //this.msg = response; this.$refs['msg-modal'].show();
        })
        .catch((err) => {
          // this.msg = err;
          // this.$refs["msg-modal"].show();
          this.$store.dispatch("modal/setModalMsg", err);
          this.$store.dispatch("modal/setModalShow", true);
        });
    },
    ...mapActions("cart", ["deleteItem", "toggleAll"]),
    deleteSelected() {
      this.$store.dispatch("cart/deleteSelected");
      this.allSelected = false;
    },
    totalQuantity() {
      //https://fred-zone.blogspot.com/2017/01/javascript-mapreduce.html
      //https://developer.mozilla.org/zh-TW/docs/Web/JavaScript/Reference/Global_Objects/Array/map
      //https://developer.mozilla.org/zh-TW/docs/Web/JavaScript/Reference/Global_Objects/Array/Reduce
      return this.items
        .map((item) => {
          // console.log("totalQuantity", item.quantity);
          if (isNaN(item.quantity) || item.quantity.length == 0) {
            // console.log("totalQuantity", 0);
            return 0;
          } else {
            // console.log("totalQuantity else", item.quantity);
            return parseInt(item.quantity, 10);
          }
        })
        .reduce((acc, cur) => acc + cur, 0);
    },
    totalAmount() {
      // return this.items.map(item => Math.round(item.listPrice * item.discount / 100) * parseInt(item.quantity, 10)).reduce((acc, cur) => acc + cur, 0);
      return this.items
        .map((item) => {
          let quantity;
          if (isNaN(item.quantity) || item.quantity.length == 0) {
            quantity = 0;
          } else {
            quantity = parseInt(item.quantity, 10);
          }
          return this.calSalePrice(item.listPrice, item.discount) * quantity;
        })
        .reduce((acc, cur) => acc + cur, 0);
    },
  },
};
</script>
